@import "scss/_variables";
@import "scss/_mixins";

.forget-password-page {
  padding: 70px 24px;

  @include breakpoint(size-lg-up) {
    padding: 130px 24px;
  }

  :global {
    .image-col {
      background-image: url(../../assets/images/lamp-bg.jpg);
      background-repeat: no-repeat;
      background-position: center center;
      background-size: cover;
      border-radius: 10px 10px 0 0;

      @include breakpoint(size-lg-up) {
        border-radius: 10px 0 0 10px;
      }

      .ant-card {
        background-color: rgba($primary-color, 0.8);

        h2.ant-typography {
          color: $white-color;
          margin-top: 0;
        }

        div.ant-typography {
          color: $white-color;
          font-size: 14px;
          line-height: 1.43;
          letter-spacing: 0.58px;
        }
      }
    }

    .form-col {
      .ant-card {
        border-radius: 0 0 10px 10px;

        @include breakpoint(size-lg-up) {
          border-radius: 0 10px 10px 0;
        }

        .title-page {
          margin-bottom: 16px;

          div.ant-typography {
            font-size: 14px;
            line-height: 1.43;
            letter-spacing: 0.58px;
          }
        }

        .forgot-password {
          .ant-btn {
            color: $primary-color;

            span {
              text-decoration: underline;
            }
          }
        }

        .ant-divider {
          border-color: #e8e8e8;
        }
      }
    }

    .send-recovery-email {
      h2.ant-typography {
        @include title-14;
        color: #1e212f;
        font-weight: normal;
        padding: 10px 8px 9px;
        border: 1px dashed #979797;
        border-radius: 5px;
      }

      h4.ant-typography,
      .ant-btn {
        font-size: 12px;
        text-align: center;
        color: $primary-color;
      }

      .ant-btn {
        span {
          text-decoration: underline;
        }
      }
    }
  }
}
