@import 'scss/_variables';
@import 'scss/_mixins';

.header {
  @include breakpoint(size-lg-up) {
    margin-bottom: 60px;
  }
  
  :global {
    .user-info {
      padding: 0 24px;
      
      .user-picture {
        font-size: 14px !important;
        background-color: #f8f8f8;
        color: $primary-color;
        font-weight: 500;
        
        .ant-avatar-string {
          opacity: 0.4;
        }
      }

      span.anticon {
        font-size: 32px;
        margin-left: 20px;
      }
    }
  }
}
