// MIXINS
//////////////////////////////////////////////////////////////////////////

// RUBIK TITLE
//////////////////////////////////////////////////////////////////////////
@mixin title-rubik {
  font-family: Rubik, sans-serif;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  font-weight: 500;
}

// TITLE 10px
@mixin title-10 {
  @include title-rubik;
  font-size: 10px;
  letter-spacing: 0.42px;
  color: $gray-normal-color;
}

@mixin title-10-primary {
  @include title-10;
  color: $primary-color;
}

// TITLE 11px
@mixin title-11 {
  @include title-rubik;
  font-size: 11px;
  letter-spacing: 0.5px;
  color: $gray-normal-color;
}

@mixin title-11-primary {
  @include title-11;
  color: $primary-color;
}

// TITLE 12px
@mixin title-12 {
  @include title-rubik;
  font-size: 12px;
  letter-spacing: 0.38px;
  color: $gray-normal-color;
}

@mixin title-12-primary {
  @include title-12;
  color: $primary-color;
}

// TITLE 14px
@mixin title-14 {
  @include title-rubik;
  font-size: 14px;
  letter-spacing: 0.44px;
  color: $gray-normal-color;
}

@mixin title-14-primary {
  @include title-14;
  color: $primary-color;
}

// TITLE 16px
@mixin title-16 {
  @include title-rubik;
  font-size: 16px;
  letter-spacing: 0.5px;
  color: $gray-normal-color;
}

@mixin title-16-primary {
  @include title-16;
  color: $primary-color;
}

// TITLE 24px
@mixin title-24 {
  @include title-rubik;
  font-size: 24px;
  letter-spacing: 0.75px;
  color: $gray-normal-color;
}

@mixin title-24-primary {
  @include title-24;
  color: $primary-color;
}

// TITLE SPECIFIC STYLE
//////////////////////////////////////////////////////////////////////////
@mixin title-border {
  position: relative;
  margin-top: 24px;
  margin-bottom: 16px;

  &:before {
    content: '';
    display: block;
    position: absolute;
    top: -5px;
    left: 0;
    width: 41px;
    height: 1px;
    border: solid 1px $third-color;
    border-radius: 50px;
  }
}

@mixin title-center {
  text-align: center;

  &:before {
    left: 50% !important;
    transform: translate(-50%, 0);
  }
}

// TITLE VALUE
//////////////////////////////////////////////////////////////////////////
@mixin title-value {
  h3.ant-typography {
    @include title-12;
  }

  h4.ant-typography {
    @include title-14-primary;
  }
}

@mixin title-value-reverse {
  h3.ant-typography {
    @include title-12-primary;
  }

  h4.ant-typography {
    @include title-12;
  }
}

// RALEWAY PARAGRAPH
//////////////////////////////////////////////////////////////////////////
@mixin paragraph-raleway {
  font-family: Raleway, sans-serif;
  color: $black-color;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  margin: 0;
}

@mixin paragraph-10 {
  @include paragraph-raleway;
  font-size: 10px;
  line-height: 1.6;
  letter-spacing: 0.5px;
}

@mixin paragraph-12 {
  @include paragraph-raleway;
  font-size: 12px;
  line-height: 1.67;
  letter-spacing: 0.5px;
}

@mixin paragraph-14 {
  @include paragraph-raleway;
  font-size: 14px;
  line-height: 1.43;
  letter-spacing: 0.58px;
}

@mixin paragraph-16 {
  @include paragraph-raleway;
  font-size: 16px;
  line-height: 1.38;
  letter-spacing: 0.67px;
}

// BREAKPOINT
//////////////////////////////////////////////////////////////////////////

// BREAKPOINTS
$size-xs: 480px;
$size-sm: 576px;
$size-md: 768px;
$size-lg: 992px;
$size-xl: 1200px;
$size-xxl: 1600px;

$breakpoints: ("size-xs": "480", "size-sm": "576", "size-md": "768", "size-lg": "992", "size-xl": "1200", "size-xxl": "1600");

@mixin breakpoint($point) {
  @if $point=='size-xs-up' {
    @media only screen and (min-width: 480px) {
      @content ;
    }
  }

  @else if $point=='size-sm-up' {
    @media only screen and (min-width: 576px) {
      @content ;
    }
  }

  @else if $point=='size-md-up' {
    @media only screen and (min-width: 768px) {
      @content ;
    }
  }

  @else if $point=='size-lg-up' {
    @media only screen and (min-width: 992px) {
      @content ;
    }
  }

  @else if $point=='size-xl-up' {
    @media only screen and (min-width: 1200px) {
      @content ;
    }
  }

  @else if $point=='size-xxl-up' {
    @media only screen and (min-width: 1600px) {
      @content ;
    }
  }

  @else if $point=='size-xs-down' {
    @media only screen and (max-width: 479px) {
      @content ;
    }
  }

  @else if $point=='size-sm-down' {
    @media only screen and (max-width: 575px) {
      @content ;
    }
  }

  @else if $point=='size-md-down' {
    @media only screen and (max-width: 767px) {
      @content ;
    }
  }

  @else if $point=='size-lg-down' {
    @media only screen and (max-width: 991px) {
      @content ;
    }
  }

  @else if $point=='size-xl-down' {
    @media only screen and (max-width: 1199px) {
      @content ;
    }
  }

  @else if $point=='size-xxl-down' {
    @media only screen and (max-width: 1599px) {
      @content ;
    }
  }
}

// MORE INFO CARD
//////////////////////////////////////////////////////////////////////////
@mixin card-more-info {
  background-color: $bg-gray;
  border: 1px solid rgba($primary-color, 0.15);
  &:before {
    content: "";
    display: block;
    position: absolute;
    left: 70px;
    top: -22px;
    width: 30px;
    height: 30px;
    background-image: url(../assets/icons/triangle-stroke.svg);
    background-size: 100% auto;
    background-repeat: no-repeat;
    background-position: center center;
  }

  .ant-card-body {
    padding: 16px;
  }
}

// BOX SHADOW
//////////////////////////////////////////////////////////////////////////
@mixin box-shadow {
  position: relative;
  
  &:after {
    content: "";
    display: block;
    position: absolute;
    height: 70px;
    width: 90%;
    left: 50%;
    bottom: 0;
    box-shadow: 0 12px 24px 0 rgba(164, 175, 209, 0.4);
    transform: translate(-50%, 0);
    z-index: -10;
  }
}