@import 'scss/_variables';
@import 'scss/_mixins';

.footer {
  background-color: transparent;
  padding: 50px 24px 0;

  :global {
    img {
      margin-bottom: 8px;
    }

    h2.ant-typography, h2, h3 {
      @include title-16-primary;
      @include title-border;
      font-size: 18px;
    }

    div.ant-typography, p {
      @include paragraph-14;
      color: $gray-normal-color;
      font-weight: 500;
      line-height: 1.57;
      letter-spacing: 0.44px;
      margin-bottom: 16px;
    }

    .policy-section {
      .policy-link, a {
        @include title-14;
        text-decoration: underline;
        display: block;
      }
    }

    .footer-info {
      ul {
        padding: 0;
        margin: 0;
        list-style: none;
        display: flex;
        align-items: center;
        justify-content: space-between;

        li {
          a {
            @include title-14;
            text-decoration: underline;
            display: block;
            padding: 0 7px;
          }
        }
      }
    }

    .footer-links {
      .link-bold,
      .link-normal {
        @include title-14-primary;
        display: block;
      }

      .link-bold {
        margin-bottom: 16px;

        @include breakpoint(size-lg-up) {
          margin-bottom: 82px;
        }
      }

      .link-normal {
        font-weight: normal;
        margin-bottom: 8px;
      }
    }
  }
}
