@import 'scss/_variables';
@import 'scss/_mixins';

.help-box {
  margin-top: 24px;
  cursor: pointer;
  :global {
    .ant-card-body {
      padding: 16px;
    }

    .ant-avatar {
      &.support-icon {
        img {
          width: auto;
          height: 100%;
        }
      }
    }

    h3,
    h2.ant-typography {
      @include title-16-primary;
      margin-bottom: 5px;
    }

    p,
    div.ant-typography {
      @include title-12-primary;
      line-height: 1.25;
      font-weight: normal;
    }
  }
}
