@import 'scss/_variables';
@import 'scss/_mixins';

.right-rail {
  :global {
    .user-picture {
      font-size: 27px !important;
      background-color: #f8f8f8;
      color: $primary-color;
      font-weight: 500;

      .ant-avatar-string {
        opacity: 0.4;
      }
    }

    .empty-box {
      background-color: #f3f3f3;

      .ant-card-body {
        padding-top: 60px;
        padding-bottom: 60px;
      }

      div.ant-typography {
        @include paragraph-14;
      }
    }

    .user-supplies {
      h2.ant-typography {
        margin-bottom: 16px;

        span.ant-typography {
          font-weight: normal;
        }

        span.anticon {
          font-size: 24px;
          margin-top: -7px;
        }

        .ant-btn.ant-btn-text.ant-btn-sm {
          padding: 0;
          float: right;

          span.anticon {
            margin: 0;
            font-size: 18px;
          }
        }
      }
    }

    .gas-box,
    .light-box {
      h2.ant-typography {
        @include title-14-primary;
        min-height: 40px;
      }

      h3.ant-typography {
        margin-bottom: 0;
        letter-spacing: normal;
        span.ant-typography {
          font-weight: normal;
        }
        .ant-badge {
          span.ant-badge-status-dot {
            width: 5px;
            height: 5px;
          }

          span.ant-badge-status-text {
            margin: 0;
          }
        }
      }

      h4.ant-typography {
        margin-top: 16px;
        color: #535252;
      }
    }

    .gas-box {
      background-color: $bg-blue-gas;
      .ant-avatar-image {
        box-shadow: 4px 7px 8px 0 rgba(5, 186, 238, 0.35);
        border-radius: 10px;
      }
    }

    .light-box {
      background-color: $bg-yellow-light;
      .ant-avatar-image {
        box-shadow: 4px 7px 8px 0 rgba(242, 145, 0, 0.35);
        border-radius: 10px;
      }
    }

    .news-section {
      h2.ant-typography {
        span.anticon {
          font-size: 28px;
          margin-top: -3px;
          margin-right: 3px;
        }

        .ant-btn.ant-btn-text.ant-btn-sm {
          padding: 0;
          float: right;

          span.anticon {
            margin: 0;
            font-size: 18px;
          }
        }
      }
    }
  }
}
