@import "scss/_variables";
@import "scss/_mixins";

.drawer {
  :global {
    .ant-drawer-mask {
      background-color: rgba($black-color, 0.7);

      @include breakpoint(size-sm-down) {
        backdrop-filter: blur(3px);
      }
    }

    .ant-drawer-content-wrapper {
      background-color: $bg-gray;
      width: 500px !important;
      height: 100% !important;

      @include breakpoint(size-sm-down) {
        width: 100% !important;
        height: 90% !important;
        top: auto;
        bottom: 0;
        border-radius: 10px 10px 0 0;
      }

      .ant-drawer-content {
        background-color: transparent;

        .ant-drawer-close {
          span.anticon {
            background-color: $bg-gray;
            font-size: 28px;
          }
        }

        .ant-drawer-body {
          padding: 16px 24px;

          @include breakpoint(size-sm-down) {
            border-radius: 10px 10px 0 0;
          }
        }

        .ant-drawer-footer {
          padding: 16px 24px;
          border-color: #cbcbcb;
        }
      }
    }
  }
}

.drawer-help {
  :global {
    .ant-drawer-content-wrapper {

      .ant-drawer-content {

        .ant-drawer-body {
          padding: 50px 90px 24px 40px;
          margin-bottom: 100px;

          @include breakpoint(size-sm-down) {
            padding: 24px;
          }

          .ant-divider.ant-divider-horizontal {
            border-color: #e8e8e8;
            margin: 16px 0;
          }

          .ant-collapse {
            background-color: transparent;
            margin-top: 24px;

            .ant-collapse-item {
              background-color: $white-color;
              border-radius: 10px;
              border: 0;
              margin-bottom: 16px;
              @include box-shadow;

              &:after {
                height: 30px;
              }

              .ant-collapse-header {
                h2.ant-typography {
                  font-weight: normal;
                  margin: 3px 0;
                }
              }

              .ant-collapse-content {
                .ant-collapse-content-box {
                  padding-top: 0;

                  div.ant-typography {
                    @include paragraph-14;
                  }
                }
              }

              &.ant-collapse-item-active {
                .ant-collapse-header {
                  padding-bottom: 0;

                  h2.ant-typography {
                    font-weight: 500;
                  }
                }
              }
            }
          }

          .drawer-contact-us {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            margin: 16px 24px;

            @include breakpoint(size-sm-up) {
              margin: 16px 90px 16px 40px;
            }

            .ant-card-body {
              padding: 16px 24px;

              div.ant-typography {
                @include paragraph-16;
                color: $primary-color;
              }
            }
          }
        }
      }
    }
  }
}

.drawer-supply {
  :global {
    .ant-drawer-content-wrapper {
      background-color: $white-color;
      width: 550px !important;
    }
    
    .info-section {
      padding: 16px;
      border-radius: 8px;
      margin-top: 16px;
      margin-bottom: 24px;
      border: 1px solid rgba($primary-color, 0.15);
      background-color: $bg-gray;
    }

    .more-info {
      border-radius: 4px;
      padding: 8px 16px 8px 24px;
      border: 1px solid rgba($primary-color, 0.15);
      background-color: $bg-gray;
      position: absolute;

      &:before {
        content: "";
        display: block;
        position: absolute;
        background-image: url(../../assets/icons/triangle-stroke.svg);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        transform: rotate(-90deg);
        left: -8px;
        top: 10px;
        width: 15px;
        height: 15px;
      }

      div.ant-typography {
        @include paragraph-14;
      }
    }
  }
}